import React, { Component } from 'react';
import Messenger from './core/Messenger';
import Header from './components/Header';

class Home extends Component {
    constructor(props){
        super(props);
        this.state = {
            user:{
                name: "Đang xử lý...",
                email:"Please wait..."
            }
        };
    }
    render() {
        let {user} = this.state;
        let nav = (!user.access_token)? null : (
            <Header active="home" />
        );
        return (
            <div className="lab lab0">
                <div className="lab-content">
                    <h1 className="text-center">{user.name}</h1>
                    <p className="text-center">{user.email}</p>
                    {nav}

                    <div className="copyright">
                        <div className="text-center">
                            Copyright &copy; 2018 Le Ngoc Doan
                        </div>
                    </div>
                </div>
            </div>
        );
    }
    
    componentDidMount(){
        this.ioEventListener();
        if(Messenger.checkLogin()){
            this.setState({user:Messenger.user});
        }else if(Messenger.access_token){
            this.emit('verify-account', {
                data:{
                    access_token:Messenger.access_token,
                },
                success:'check-auth-home-success',
                fail:'check-auth-home-fail',
            });
        }else{
            this.props.history.push('/login');
        }
    
        console.log('Home rendered');
        
        
    }

    emit = (event, data) => {
        Messenger.socket().emit(event, data);
    }

    
    ioEventListener = () => {
        const socket = Messenger.socket();
        // dang nhap thanh cong
        socket.on('check-auth-home-success', (user) => {
            let {access_token} = user;
            Messenger.set({user, access_token:access_token});
            this.setState({user});
        });

        socket.on('check-auth-home-fail', (data) => {
            this.props.history.push('/login');
        });

        
        
    }
}

export default Home;



